<template>
    <div class="init">
        <div class="content">
            <p>我是谁?</p>
            <input type="text" v-model="person"><br/>
            <span @click="sure">确定</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "init",
        data(){
            return{
                person:'',
            }
        },
        methods:{
            sure(){
                if(this.person === '万峰'){
                    this.$router.push({path:`/zff`});
                }
                else {
                    alert("输入错误");
                }
            }
        }
    }
</script>

<style scoped lang="less">
    @import "../assets/css/vrem.less";
    .init{
        .content{
          text-align: center;
            p{
                .toRem(font-size,80);
                color:pink;
                text-align: center;
            }
            input{
                width: 90%;
                margin:0 auto;
                .toRem(margin-top,40);
                .toRem(margin-bottom,40);
                .toRem(line-height,100);
            }
            span{
                display:inline-block;
                .toRem(padding-top,10);
                .toRem(padding-bottom,10);
                .toRem(padding-left,30);
                .toRem(padding-right,30);
                background-color: pink;
                color:white;
            }
        }
    }
</style>


