import Vue from 'vue'
import Router from 'vue-router'
import Xrr from '@/components/Xrr'
import Init from '@/components/Init'

Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'init',
      component: Init
    },
    {
        path:'/zff',
        name: 'xrr',
        component: Xrr
    }
  ]
})



