<template>
    <div class="xrr">
        <!--<audio src="http://www.ytmp3.cn/down/50494.mp3" loop="loop" autoplay="autoplay">-->
        <audio src=" http://www.170mv.com/kw/other.web.nf01.sycdn.kuwo.cn/resource/n3/61/60/2914044483.mp3" loop="loop" autoplay="autoplay">
            Your browser does not support the audio element.
        </audio>
        <p class="x0">你一定很好奇这是什么吧？</p>
        <p class="x1">请往下看...</p>
        <img src="@/assets/xrr/pointer.gif" alt="">

        <img class="dinner" src="@/assets/xrr/1.jpeg" alt="">
        <p class="x2">我们在一起的日子...</p>
        <img class="inter" src="@/assets/xrr/inter.gif" alt="">
        <p class="who">很怀念....</p>

        <p class="other">
            啦啦啦...
        </p>

        <img class="dinner" src="@/assets/xrr/2.jpeg" alt="">
        <img class="dinner" src="@/assets/xrr/3.jpeg" alt="">
        <img class="dinner" src="@/assets/xrr/4.jpeg" alt="">
        <img class="dinner" src="@/assets/xrr/5.jpeg" alt="">
        <img class="dinner last" src="@/assets/xrr/6.jpeg" alt="">
        <p class="x10">哈哈.....认识你很幸运，OY！！</p>


        <div class="textArea">
            <p class="extra">附:</p>
            <p class="homework">
                希望你的每一天都快乐，要幸福
            </p>
        </div>
        <!--<img class="inter" src="/static/images/xrr/inter.gif" alt="">-->
        <!--<p class="who">是谁是谁你是谁....</p>-->

        <div class="former">
            曾经的点滴...
        </div>

        <div class="addArea">
            地点<br/>
            <input type="text" class="addAdress" v-model="addAdress"> <br/><br/>
            人物<br/>
            <input type="text" class="addWho" v-model="addWho"> <br/><br/>
            故事<br/>
            <input type="text" class="addStory" v-model="addStory"> <br/><br/>
            <span class="sure3" @click="add">添加</span>
        </div>

        <table class="table">
             <tr>
                 <th style="width:35%;">地点</th>
                 <th style="width:35%;">故事</th>
                 <th style="width:30%;">操作</th>
             </tr>
             <tr v-for="(item,index) in db" :key = index>
                 <td>{{ item.adress }}</td>
                 <td>{{ item.story }}</td>
                 <td class="operator">
                     <span @click="detail(item._id,item.adress,item.person,item.story)">详情</span>
                     <span @click="modify(item.adress,item.story,item._id,item.person)">修改</span>
                     <span @click="deleteOne(item._id)">删除</span>
                 </td>
             </tr>
         </table>
         <div class="remind" v-if="db.length===0">暂无数据</div>

        <div class = "sure1" @click = "searchAll">显示所有数据</div><br/>

        <div class="remove" @click = "remove">删除所有数据</div><br/><br/>

        <input type="text" class="searchOne" v-model="adress"><label class="personFirst">&nbsp;&nbsp;&nbsp;按地点查询首条数据</label><span class = "sure2" @click = "sure2">确定</span><br/><br/>
        <input type="text" class="searchAll" v-model="adressAll"><label class="personFirst">&nbsp;&nbsp;&nbsp;按地点查询所有数据</label><span class = "sure2" @click = "sure1">确定</span><br/><br/>

        <div v-show = 'isShow' class="modifyTable">
            地点<br/><br/>
            <input type="text" class="addAdress" v-model = "modifyAdress"> <br/><br/>
            人物<br/><br/>
            <input type="text" class="addWho" v-model = "modifyPerson"> <br/><br/>
            故事<br/><br/>
            <input type="text" class="addStory" v-model="modifyStory"> <br/><br/>
            <p class="confirmModify" @click="confirmModify">修改</p>
        </div>

        <div class="detail clearfix" v-show="detailisShow">
            <p>地点</p>
            <p>{{ detailAdress }}</p><br/><br/>
            <p>人物</p>
            <p>{{ detailPerson }}</p><br/><br/>
            <p>故事</p>
            <p>{{ detailStory }}</p><br/><br/>

            <span @click="close" class="fr">关闭</span>
        </div>
        <!--<table v-show = 'isShow' class="modifyTable">-->
            <!--<tr>-->
                <!--<th>地点</th>-->
                <!--<th>故事</th>-->
                <!--<th rowspan="2" @click="confirmModify">修改</th>-->
            <!--</tr>-->
            <!--<tr>-->
                <!--<td>-->
                    <!--<input type="text" v-model = "modifyAdress">-->
                <!--</td>-->
                <!--<td>-->
                    <!--<input type="text" v-model = "modifyStory">-->
                <!--</td>-->
            <!--</tr>-->
        <!--</table>-->

    </div>
</template>
<script>
    export default {
        name: "xrr",
        data(){
            return {
                db:[],
                adress:'',
                addAdress:'',
                addStory:'',
                addWho:'',
                adressAll:'',
                id:'',
                modifyAdress:'',
                modifyStory:'',
                modifyPerson:'',
                detailAdress:'',
                detailStory:'',
                detailPerson:'',
                isShow:false,
                detailisShow:false,
            }
        },
        created(){
            // this.$axios.get('/api/lottery/d11x5/recent_list', {
            //     params:{
            //         lottery_id:201,
            //     }
            // }).then(res => {
            //     let data = res.data;
            //     if (data.code === 200) {
            //         console.log(data);
            //     } else {
            //         console.log("接口请求错误");
            //     }
            // }).catch(error => {
            //     console.log(error)
            // });
            let wx = require('weixin-js-sdk');
            wx.config({
                debug: false,
                appId: data.appid,
                timestamp: data.timestamp,
                nonceStr: data.noncestr,
                signature: data.signature,
                jsApiList: []
            })
            wx.ready(function() {
                // 在微信的ready中进行播放 不管成功配置与否 都会执行ready
                document.getElementById('musicAudio').setAttribute('src', 'http://www.170mv.com/kw/other.web.nf01.sycdn.kuwo.cn/resource/n3/61/60/2914044483.mp3')
                document.getElementById('musicAudio').play()
            });

          //默认加载所有用户信息
             this.searchAll();
        },
        methods:{
            //查看用户详情
            detail(id,adress,person,story){
                this.detailisShow = true;

                this.detailAdress = adress;
                this.detailPerson = person;
                this.detailStory = story;
            },
            //关闭用户详情
            close(){
                this.detailisShow = false;
            },
            //修改用户信息
            modify(adress,story,id,person){
                this.modifyAdress = adress;
                this.modifyStory = story;
                this.modifyPerson = person;
                this.id = id;
                this.isShow = true;
            },
            //确认修改
            confirmModify(){
                //修改某条用户信息
                this.$axios.get('/mongodb/updateOne', {
                    params:{
                        id:this.id,
                        adress:this.modifyAdress,
                        story:this.modifyStory,
                        person:this.modifyPerson,
                    }
                }).then(res => {
                    if(res.data.n === 1){
                        // this.searchAll();
                        //不进行数据库查询，前端页面自动更新
                        this.db.forEach((ele)=>{
                            if(ele._id === this.id){
                                ele.adress = this.modifyAdress;
                                ele.story = this.modifyStory;
                                ele.person = this.modifyPerson;
                            }
                        });
                    }
                }).catch(error => {
                    console.log(error)
                });
                this.isShow = false;
            },
            //查询所有数据
            searchAll(){
                this.$axios.get('/mongodb/find', {
                }).then(res => {
                    this.db = res.data;
                    // this.age = '';
                }).catch(error => {
                    console.log(error)
                });
            },
            //查询满足条件的所有用户信息
            sure1(){
                this.$axios.get('/mongodb/findAccess', {
                    params:{
                        adress:this.adressAll,
                    }
                }).then(res => {
                    this.db = res.data;
                    this.adressAll = '';
                }).catch(error => {
                    console.log(error)
                });
            },
            //查询首条满足条件的用户信息
            sure2(){
                this.$axios.get('/mongodb/findOne', {
                    params:{
                        adress:this.adress,
                    }
                }).then(res => {
                    this.db = [];
                    if(res.data){
                        this.db.push(res.data);
                        this.adress = '';
                    }

                }).catch(error => {
                    console.log(error)
                });
            },
            //添加某条用户信息
            add(){
                this.$axios.get('/mongodb/insertOne', {
                    params:{
                        adress:this.addAdress,
                        story:this.addStory,
                        person:this.addWho,
                    }
                }).then(res => {
                    console.log(res);
                    if(res.data.n === 1){
                        this.searchAll();
                        //不进行数据库查询，前端页面自动更新
                        // this.db.push({
                        //     name:this.addAdress,
                        //     age:this.addStory,
                        // });
                        this.addAdress = '';
                        this.addStory = '';
                        this.addWho = '';
                    }
                }).catch(error => {
                    console.log(error)
                });
            },
            //删除某条用户信息
            deleteOne(id,index){
                this.$axios.get('/mongodb/deleteOne', {
                    params:{
                        id:id,
                    }
                }).then(res => {
                    console.log(res);
                    if(res.data.n === 1){
                        // this.searchAll();
                        //不进行数据库查询，前端页面自动更新
                        this.db = this.db.filter((item)=>{
                            return item._id !== id;
                        });
                    }
                }).catch(error => {
                    console.log(error)
                });
            },
            //删除所有用户信息
            remove(){
                this.$axios.get('/mongodb/remove', {
                }).then(res => {
                    console.log(res);
                    if(res.data.ok === 1){
                        // this.searchAll();
                        //不进行数据库查询，前端页面自动更新
                        this.db = [];
                    }
                }).catch(error => {
                    console.log(error)
                });
            }
        }
    }
</script>

<style scoped lang="less">
    @import "../assets/css/vrem.less";
    .xrr{
        background-color: #fff;
        .toRem(padding-bottom,100);
    }
    .dinner{
        width:100%;
        .toRem(margin-bottom,30);
    }
    .last{
        margin-bottom:0;
    }
    .x0{
        .toRem(padding,60);
        .toRem(font-size,100);
        color:pink;
    }
    .x1{
        .toRem(font-size,60);
        text-align: center;
        color:#000000;
    }
    .x2{
        .toRem(font-size,50);
        .toRem(padding-left,40);
    }
    .x10{
        .toRem(font-size,50);
        text-align: center;
        .toRem(line-height,100);
        color:pink;
    }
    .inter{
        width:50%;
        .toRem(margin-top,30);
        margin-left:50%;
        transform:translateX(-50%);
    }
    .who{
        text-align: center;
        .toRem(font-size,50);
        .toRem(margin-top,20);

    }
    .other{
        .toRem(font-size,50);
        .toRem(padding-left,40);
        .toRem(margin-top,80);
        .toRem(margin-bottom,50);
    }
    .textArea{
        width:90%;
        margin:0 auto;
        .toRem(margin-top,100);
        .toRem(margin-bottom,40);
        /*<!--.toRem(margin-bottom,100);-->*/
        .toRem(height,180);
        border:1px solid lightBlue;
        .extra{
            .toRem(font-size,30);
            .toRem(line-height,80);
            .toRem(padding-left,40);
        }
        .homework{
            .toRem(padding-left,40);
            .toRem(font-size,30);
        }
    }
    .table{
        .toRem(font-size,30);
        width:90%;
        margin-left:auto;
        margin-right:auto;
        .toRem(margin-top,40);
        .toRem(margin-bottom,40);
        border: 1px solid black;
        border-collapse: collapse;
        td{
            border:1px solid black;
            .toRem(line-height,80);
            text-align:center;
            .toRem(padding-top,40);
            .toRem(padding-bottom,40);
            .toRem(padding-left,10);
            .toRem(padding-right,10);

            /*overflow: hidden;*/
            /*white-space: nowrap;*/
            /*text-overflow: ellipsis;*/
        }
        th{
            .toRem(line-height,80);
        }

    }
    .searchOne{
        .toRem(font-size,30);
        .toRem(width,300);
        .toRem(margin-left,70);
    }
    .searchAll{
        .toRem(font-size,30);
        .toRem(width,300);
        .toRem(margin-left,70);
    }

    .personFirst{
        .toRem(font-size,30);
    }
    .sure2{
        .toRem(font-size,30);
        display:inline-block;
        .toRem(margin-left,80);
        background-color: lightblue;
        color:white;
    }
    .remind{
        .toRem(font-size,30);
        .toRem(margin-left,70);
    }
    .sure1{
        .toRem(font-size,30);
        .toRem(margin-left,70);
        display:inline-block;
        background-color: lightblue;
        color:white;
        .toRem(margin-bottom,40);
    }
    .remove{
        .toRem(font-size,30);
        .toRem(margin-left,70);
        display:inline-block;
        background-color: lightblue;
        color:white;
        /*<!--.toRem(margin-bottom,40);-->*/
    }
    .addArea{
        border:1px solid black;
        .toRem(padding,40);
        .toRem(margin-left,70);
        .toRem(margin-right,70);
    }
    .addAdress{
        .toRem(font-size,30);
        /*<!--.toRem(margin-left,70);-->*/
        /*<!--.toRem(width,800);-->*/
        width:100%;
    }
    .addWho{
        .toRem(font-size,30);
        /*<!--.toRem(margin-left,70);-->*/
        /*<!--.toRem(width,800);-->*/
        width:100%;
    }
    .addStory{
        .toRem(font-size,30);
        /*<!--.toRem(margin-left,70);-->*/
        /*<!--.toRem(width,800);-->*/
        width:100%;
    }
    .sure3{
        .toRem(font-size,30);
        /*<!--.toRem(margin-left,70);-->*/
        display:inline-block;
        background-color: lightblue;
        color:white;
    }
    td{
        .toRem(height,100);
        .toRem(line-height,100);
    }
    .operator{
        text-align: center;
        span{
            color:white;
            display:block;
            background-color: skyblue;
            .toRem(margin-bottom,20);
            &:nth-child(3){
                margin-bottom:0;
            }
        }

            padding-left:0;
            padding-right:0;

    }
    .modifyTable{
        border:1px solid black;
        .toRem(padding,40);
        border-collapse: collapse;
        .toRem(font-size,30);
        width:96%;
        position:fixed;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        background-color: lightgrey;
        input{
            width:90%;
        }
        .confirmModify{
            .toRem(font-size,30);
            display:inline-block;
            .toRem(padding-top,10);
            .toRem(padding-bottom,10);
            .toRem(padding-left,30);
            .toRem(padding-right,30);
            background-color: lightblue;
            color:white;
        }
    }
    .former{
        .toRem(font-size,30);
        .toRem(margin-bottom,40);
        .toRem(margin-left,70);
        color:#ff4855;
    }
    .detail{
        .toRem(padding,40);
        .toRem(font-size,30);
        width:96%;
        position:fixed;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        background-color: yellowgreen;
        color:white;
        span{
            display:inline-block;
            .toRem(padding-top,10);
            .toRem(padding-bottom,10);
            .toRem(padding-left,30);
            .toRem(padding-right,30);
            background-color:orangered;
            color:white;
        }
    }
</style>


